* {
  font-family: "Segoe UI", 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* mmmsmms kkk*/
.tweet {
  background-color: #81ecec;
  border: 2px solid #2980b9;
  padding: 8px;
  width: fit-content;
  /*Ca permet de garder pill la bonne taille*/
  min-width: 320px;
  position: relative;
  border-radius: 10px;
}

.tweet .button {
  position: absolute;
  right: 0;
  top: 0;
  color: red;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.tweet h3,
p {
  margin: 0;
  margin-bottom: 8px;
  margin-top: 8px;
}

.tweet-container {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;

}

.tweetForm {
  display: flex;
  flex-direction: column;
  width: fit-content;
  gap: 8px;
  margin-bottom: 32px;
}